import { useState } from "react";

import { ComponentSpec } from "../componentSpec";

interface KubeflowPipelinesSubmitterProps {
  componentSpec?: ComponentSpec;
}

const KubeflowPipelinesSubmitter = ({
  componentSpec,
}: KubeflowPipelinesSubmitterProps) => {
  const [message, setMessage] = useState("");
  return (
    <div>
      <button
        onClick={async (e) => {
          const response = await fetch(
            "https://704a448c0a693194-dot-us-central2.pipelines.googleusercontent.com/apis/v1beta1/runs",
            {
              headers: new Headers({
              }),
            }
          );
          const responseText = await response.text();
          setMessage(responseText);
        }}
      >
        List KFP runs
      </button>
      <span>{message}</span>
    </div>
  );
};

export default KubeflowPipelinesSubmitter;
