import { useCallback, useState, DragEvent, useEffect } from "react";
import { useDropzone } from "react-dropzone";
import { getAllComponentsAsRefs, storeComponentText } from "../componentStore";
import { ComponentReference, TaskSpec } from "../componentSpec";

const onDragStart = (event: DragEvent, nodeData: object) => {
  event.dataTransfer.setData("application/reactflow", JSON.stringify(nodeData));
  event.dataTransfer.setData(
    "DragStart.offset",
    JSON.stringify({
      offsetX: event.nativeEvent.offsetX,
      offsetY: event.nativeEvent.offsetY,
    })
  );
  event.dataTransfer.effectAllowed = "move";
};

const UserComponentLibrary = () => {
  const [errorMessage, setErrorMessage] = useState("");
  const [componentRefs, setComponentRefs] = useState<ComponentReference[]>([]);

  useEffect(() => {
    getAllComponentsAsRefs().then(setComponentRefs);
  }, []);

  const onDrop = useCallback((acceptedFiles: File[]) => {
    console.debug("Dropped files: ", acceptedFiles);
    acceptedFiles.forEach((file) => {
      const reader = new FileReader();
      reader.onabort = () => console.log("file reading was aborted");
      reader.onerror = () => console.log("file reading has failed");
      reader.onload = async () => {
        console.log("Before1");
        const binaryStr = reader.result;
        if (binaryStr === null || binaryStr === undefined) {
          console.error(`Dropped file reader result was ${binaryStr}`);
          return;
        }
        try {
          const componentRef = await storeComponentText(binaryStr);
          console.debug("storeComponentText succeeded", componentRef);
          setErrorMessage("");
          const allComponentRefs = await getAllComponentsAsRefs();
          console.debug(allComponentRefs);
          setComponentRefs(allComponentRefs);
        } catch (err) {
          setErrorMessage(
            `Error parsing the dropped file as component: ${err.toString()}.`
          );
          console.error("Error parsing the dropped file as component", err);
        }
      };
      reader.readAsArrayBuffer(file);
    });
  }, []);
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <div>
      <div {...getRootProps()}>
        <input {...getInputProps()} />
        <div
          style={{ border: "1px solid black", padding: "4px", minHeight: "3em" }}
        >
          {isDragActive
            ? "Drop the files here ..."
            : errorMessage ||
              "Drag and drop component.yaml files or click to select files"}
        {componentRefs.map((componentRef) => (
          <div
            key={componentRef.digest}
            title={componentRef.digest}
            className="react-flow__node react-flow__node-task"
            draggable
            onDragStart={(event: DragEvent) => {
              const taskSpec: TaskSpec = {
                componentRef: componentRef,
              };
              return onDragStart(event, { task: taskSpec });
            }}
          >
            {componentRef.spec?.name ?? "Component"}
          </div>
        ))}
        </div>
      </div>
    </div>
  );
};

export default UserComponentLibrary;
